import React, { Component } from "react";
import ReactGa from "react-ga";
import { createMuiTheme, ThemeProvider, Divider, Box } from "@material-ui/core";
import { BackpropExplainer, Article, Acknowledge, Header, Nav } from "./components/exports";
import { Element } from "react-scroll";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom"; // Importing v5.x Router

// Example of the new page
import NewPage from "./pages/NewPage"; // Create this file as the new page

const globalWeight = 350;
const theme = createMuiTheme({
  overrides: {
    MuiTypography: {
      h1: { fontWeight: globalWeight },
      h2: { fontWeight: globalWeight },
      h3: { marginTop: 25, fontWeight: globalWeight },
      h4: { marginTop: 20, marginBottom: 10, fontWeight: globalWeight },
      h5: { fontWeight: globalWeight },
      h6: { fontWeight: globalWeight },
      body1: { fontWeight: globalWeight },
      body2: { fontWeight: globalWeight },
    },
  },
});

class App extends Component {
  componentDidMount() {
    ReactGa.initialize(`${process.env.REACT_APP_GAID}`);
    ReactGa.pageview("/");
  }

  render() {
    return (
      <ThemeProvider theme={theme}>
        <Router> {/* Wrap the app in Router */}
          <div>
            <br />
            
            <Switch> {/* Use Switch to group your routes */}
              <Route exact path="/" component={Article} />
              <Route exact path="/newpage" component={NewPage} /> {/* Define the route for NewPage */}
            </Switch>

            <br />
          </div>
        </Router>
      </ThemeProvider>
    );
  }
}

export default App;
